import React from 'react';
import { useTranslation, withTranslation, Trans } from 'react-i18next';
import './redes-general.css'

function RedesGeneral(props) {

  const { t, i18n } = useTranslation();

  return (
    <div className="redes-container">
      <h2 className="redes-title">{t('misc.followus')}</h2>
      <div className="redes-icon-container">
        {
          props.redesData.data[0].redes.map((red) => {
            return (
              <a href={red.link} key={red.id} target= {"_blank"}>
                <i className={red.fafa} style={{color:red.color}}></i>
              </a>
            )
          })
        }
      </div>
    </div>
  )
}

export default RedesGeneral;